* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #333333;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.content__wrapper {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #333333;
}

.show-runner__wrapper {
  display: flex;
  justify-content: center;
}

.show-runner__status-panel {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 0 10px;
  max-width: 400px;
  width: 100%;
}

.show-runner__status-panel--running {
  background-color: #EEEEEE;
}

.show-runner__timer {
  --status-panel-size: 100px;

  display: flex;
  align-items: center;
  justify-content: center;

  width: var(--status-panel-size);
  height: var(--status-panel-size);

  border-radius: 50%;

  -webkit-transform: translateY(-50%);

          transform: translateY(-50%);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.25)
}

.show-runner__timer-text {
  color: white;
  font-size: 2rem;
  font-weight: bold;
}

.show-runner__timer--running {
  background-color: #1DBEA6;
}

.show-runner__content {
  padding: 10px 0;
  -webkit-transform: translateY(-50px);
          transform: translateY(-50px);
  text-align: center;
}

.show-runner__motivation {
  font-size: 1.75rem;
  color: #888888;
}

.stage__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;

  height: 100vh;
}

.stage__wrapper--running {
  background: url("https://media.giphy.com/media/l9Tllo1thElT5gvVOU/giphy.gif") center center no-repeat;
  background-size: cover;
}

.stage__content {
  padding: 12px;
  margin: 10px;
  border-radius: 5px;
  flex-grow: 1;
}

.stage__form {
  display: flex;
  flex-direction: row;
}

.stage__input {
  /* Style Border */
  border: 1px solid white;
  border-radius: 3px;

  /* Focus Border */
  outline: none;

  /* Input Styling */
  font-size: 2rem;
  color: white;

  /* Padding and Size */
  padding: 0.5rem;

  background-color: transparent;
  flex-grow: 1;
}

.stage__button {
  margin-left: 5px;
  flex-basis: 200px;
}

.stage__button:hover {
  color: hotpink;
  background-color: white;
  transition: 0.25s background-color ease-out;
}
body {
  padding: 0;
  margin: 0;
  font-family: 'Montserrat', sans-serif;
}

/* Applauser Button Style */
button {
  background-color: transparent;
  border: 1px solid white;
  color: white;
  outline: none;
  border-radius: 3px;
  cursor: pointer;
}

/**
 * 1. Change the font styles in all browsers.
 * 2. Remove the margin in Firefox and Safari.
 */

 button,
 input,
 optgroup,
 select,
 textarea {
   font-family: inherit; /* 1 */
   font-size: 100%; /* 1 */
   line-height: 1.15; /* 1 */
   margin: 0; /* 2 */
 }
 
 /**
  * Show the overflow in IE.
  * 1. Show the overflow in Edge.
  */
 
 button,
 input { /* 1 */
   overflow: visible;
 }
 
 /**
  * Remove the inheritance of text transform in Edge, Firefox, and IE.
  * 1. Remove the inheritance of text transform in Firefox.
  */
 
 button,
 select { /* 1 */
   text-transform: none;
 }
 
 /**
  * Correct the inability to style clickable types in iOS and Safari.
  */
 
 button,
 [type="button"],
 [type="reset"],
 [type="submit"] {
   -webkit-appearance: button;
 }
